var CONFIG = {
  SENDGRIDURL: "https://api.sendgrid.com/v3/mail/send"
};

function sendGridEmail(key, to, from, subject, body) {
  var type = arguments.length > 5 && arguments[5] !== undefined ? arguments[5] : "text/plain";
  var isSuccess = sendEmail(key, to, from, subject, body, type);
  return isSuccess;
}

function sendEmail(key, to, from, subject, body, type) {
  return fetch(CONFIG.SENDGRIDURL, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + key
    },
    body: JSON.stringify({
      "personalizations": [{
        "to": [{
          "email": to
        }],
        "subject": subject
      }],
      "from": {
        "email": from
      },
      "content": [{
        "type": type,
        "value": body
      }]
    })
  }).then(function (response) {
    return true;
  })["catch"](function (error) {
    return false;
  });
}

exports.sendGridEmail = sendGridEmail;